<template>
  <div class="hello">
    <ul class="list-group">
      <li class="list-group-item" v-for="(list, index) in lists" :key="index">{{ list.text }}</li>
    </ul>
    <form>
    <div class="row mt-4">
      <div class="col-10">
        <div class="form-floating">
          <textarea class="form-control" placeholder="Leave a comment here" id="floatingTextarea" v-model="listText"></textarea>
          <label for="floatingTextarea">Comments</label>
        </div>
      </div>
      <div class="col-2">
        <button type="button" class="btn btn-primary col-12" v-on:click="addList()">Primary</button>
      </div>
    </div>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function () {
    return {
      listText: '',
      lists: [
        {text: 'Vue.jsを学ぶ'},
        {text: 'Vue.jsでアプリケーションをつくる'}
      ]
    }
  },
   methods: {
		addList: function() {
			var newList = this.listText.trim();
			if (!newList) {return;}
			this.lists.push(
				{text: newList}
			);
			this.listText = '';
		}
   }
}
</script>

